/* CharacterTable.css */

/* Arka plan ve tablo rengi (kan kırmızısı tema) */
.table-container {
  background-color: #1a0000; /* koyu bordo */
  padding: 20px;
  border-radius: 10px;
}

.character-table {
  width: 100%;
  border-collapse: collapse;
  background-color: #8B0000; /* kan kırmızısı */
  color: white;
  box-shadow: 0 0 20px rgba(255, 0, 0, 0.3);
}

.character-table th,
.character-table td {
  padding: 12px 15px;
  text-align: center;
  border: 1px solid #5c0000;
}

.character-table th {
  background-color: #4d0000;
}

/* Resim kapsayıcısı */
.character-image-container {
  width: 60px;
  height: 60px;
  position: relative;
  display: inline-block;
  overflow: visible;
}

.character-image {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 8px;
  transition: all 0.3s ease-in-out;
  z-index: 1;
}

/* Hover efekt: resmi gerçekten büyüt */
.character-image-container:hover .character-image {
  width: 360px;
  height: 360px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 4px solid #fff;
  z-index: 1000;
  box-shadow: 0 0 20px rgba(255, 0, 0, 0.7);
  background-color: #1a0000;
}
